<template>
    <div>
        <h1>고객등록</h1>

        <div>
        name<input v-model = "customer.name">
        </div>
        <div>
        email<input v-model = "customer.email">
        </div>
        <div>
        phone<input v-model = "customer.phone">
        </div>
        <div>
        address<input v-model = "customer.address">
        </div>
        <button @click = "addHandler">등록</button>
    </div>
</template>
<script>
    import axios from "axios";
    export default {
     data(){return{
        customer : {name : '', email : '', phone : '', address : ''}
     }},
     created() {

     },
     methods : {
        async addHandler(){
            await axios.post("/customer", this.customer)
            .then(json => this.$router.push("/customer"))
        }
     }
     }
</script>
